import * as React from "react";
import { RadialChart, Hint } from "react-vis";
import { colors } from "../../../design-system";
import { isEmpty } from "lodash";
import {
  totalParticipants,
  gender,
} from "../../../content/ambassadors-report/metrics";
import Tip from "../../../components/Tip";

interface Props {}
const Income: React.FC<Props> = () => {
  const [value, setValue]: any = React.useState({});

  return (
    <div>
      <p>Income Metrics</p>
      <RadialChart
        opacityRance={[1, 0.9, 0.8, 0.7]}
        animation
        data={gender}
        width={300}
        height={300}
        showLabels
        labelsStyle={{
          fill: colors.coldWater,
        }}
        onValueMouseOver={(v: any) => {
          setValue({
            data: v,
            title: gender.find(item => item.label === v.label).label,
            metrics: [
              {
                label: "Total",
                value: Math.floor(
                  totalParticipants *
                    (gender.find(item => item.label === v.label).angle * 0.01),
                ),
              },
              {
                label: "Percentage",
                value: `${gender.find(item => item.label === v.label).angle}%`,
              },
            ],
          });
        }}
        onSeriesMouseOut={(v: any) => setValue({})}
      >
        {!isEmpty(value) && (
          <Hint value={value.data}>
            <Tip title={value.title} metrics={value.metrics} />
          </Hint>
        )}
      </RadialChart>
    </div>
  );
};

export default Income;
